* {
  box-sizing: border-box;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; }

body {
  background-color: #ace9eb; }

.MainText {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 10px; }

.Options {
  display: flex;
  justify-content: center; }

.ChatBlock {
  width: 900px;
  height: 600px;
  border-radius: 20px;
  border: 1px solid black;
  display: flex;
  background-color: white; }

.ChatBlock__SiteBar {
  width: 30%;
  height: 100%;
  border-right: 1px solid black; }

.ChatBlock__UserContent {
  width: 70%;
  height: 100%; }

.ChatBlock__Search {
  width: 100%;
  height: 70px;
  border-bottom: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: center; }

.ChatBlock__SearchFriend {
  width: 80%;
  height: 40px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid black;
  padding-left: 30px; }

.ChatBlock__SearchFriend:focus {
  border: 1px solid #522ef5; }

.ChatBlock__FriendsList {
  width: 100%;
  height: 510px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto; }

.ChatBlock__Communication {
  width: 100%;
  height: 520px; }

.ChatBlock__UserMessage {
  width: 100%;
  height: 70px;
  border-top: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: center; }

.FriendVisual {
  min-height: 60px;
  width: 88%;
  border-radius: 7px;
  margin-top: 10px;
  padding: 5px;
  display: flex;
  align-items: center; }

.FriendVisual:hover {
  background-color: rgba(161, 202, 248, 0.521);
  cursor: pointer; }

.ChatBlock__FriendAvatar {
  width: 50px;
  height: 50px;
  background-color: #f56d2e;
  border-radius: 5px; }

.ChatBlock__FriendNameText {
  margin-left: 30px; }

.ChatBlock_FriendAvatarImg {
  width: 50px;
  height: 50px;
  border-radius: 5px; }

.ChatBlock__UserMessageInput {
  width: 80%;
  height: 40px;
  padding-left: 10px; }

.ChatBlock__UserMessageButton {
  height: 40px;
  background-color: white; }

.ChatBlock__UserMessageButtonImages {
  width: auto;
  height: 100%; }
